.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

.page-btn {
    padding: 10px 12px;
    margin: 0 5px;
    border: none;
    cursor: pointer;
    background-color: var(--secondary-color);
    color: var(--primary-color);
    border-radius: 5px;
}

.page-btn.active {
    background-color: var(--active-page-color);
    font-weight: bold;
}

.page-btn:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}


@media (max-width: 768px) {
    .pagination button {
        font-size: 14px;
    }
}